<template>

  <div class="col-lg-6 offset-lg-3">
        <h1 class="titulo">{{ registrationMode ? "Adicionar" : "Editar" }} Adopter</h1>
        <div class="col-sm-10">
          <hr />
      </div>
    
      <div class="col-sm-10">
        <div class="form-group">
          <label for="id">Código</label>
          <input id="id" v-model="adopter.id" type="text" disabled class="form-control" />
        </div>
      </div>

      <div class="col-sm-10">
        <div class="form-group">
          <label for="name">Nome</label>
          <input id="name" v-model="adopter.name" type="text" class="form-control" />
        </div>
      </div>
 
      <div class="col-sm-10">
        <div class="form-group">
          <label for="name">Email</label>
          <input id="email" v-model="adopter.email" type="email" class="form-control" />
        </div>
      </div>

     <div class="col-sm-10">
        <div class="form-group">
          <label for="name">Telefone</label>
          <input id="telefone" v-model="adopter.telephone" type="nunber" class="form-control" />
        </div>
      </div>

      <div class="col-sm-10">
        <div class="form-group">
          <label for="name">Nome do Pet</label>
          <input id="description" v-model="adopter.description" type="text" class="form-control" />
        </div>
      </div>

      <div class="row">
        <div class="col-sm-10">
          <hr />
      </div>

      <div class="col-sm-10">
        <div v-show="registrationMode" class="form-check-inline">
          <label class="form-check-label">
            <input v-model="continueAdding"  type="checkbox" class="form-check-input"  />
            Continuar adicionando
          </label>
        </div>

      

        <button  @click="cancelAction" class="btn btn-default float-right" type="button">
          Cancelar
        </button>
        <button @click="save" class="btn btn-primary float-right mr-2" type="button">
          Salvar
        </button>
      </div>
    </div>
  </div>
</template>

<script>

import Adopter from '../models/Adopter';
import adopterService from '../services/adopter-service';

export default {
    name:"AdopterForm",
    data(){
        return {

            adopter: new Adopter(),
            registrationMode: true,
            continueAdding: false,
        }
    },
    mounted(){
        let id = this.$route.params.id;
        if(!id) return;

        this.registrationMode = false;
        this.getProductById(id);
    },
    methods:{
        getProductById(id){
            adopterService.getById(id)
            .then(response => {
                this.adopter = new Adopter(response.data);
            })
            .catch(error =>{
                console.log(error)
                this.$swal({
                  icon: 'error',
                  title: 'Não foi possível obter o produto pelo id ' + id,
                  confirmButtonColor: '#FF3D00',
                  animate: true
                });
            })
        },

        cancelAction(){
            this.adopter = new Adopter();
            this.$router.push({name: "AdopterController"})
        },
        registerAdopter(){
            if(!this.adopter.modelValidForRegistration()){
                this.$swal({
                  icon: 'warning',
                  title: 'O nome do produto é obrigatório para o cadastro.',
                  confirmButtonColor: '#0039A8',
                  animate: true
                });
                return;
            }

            adopterService.register(this.adopter)
            .then(() => {
                this.$swal({
                  icon: 'success',
                  title: 'Adotantet cadastrado com sucesso!',
                  confirmButtonColor: '#0039A8',
                  animate: true
                });

                this.adopter = new Adopter();

                if(!this.continueAdding){
                    this.$router.push({name: "AdopterController"})
                }
            })
            .catch(error =>{
                console.log(error);
                this.$swal({
                  icon: 'error',
                  title: 'Não foi possível cadastrar o produto',
                  confirmButtonColor: '#0039A8',
                  animate: true
                });
            });
        },

        updateAdopter(){
            if(!this.adopter.modelValidToUpdate()){
                this.$swal({
                  icon: 'warning',
                  title: 'O código e nome do adotante são obrigatórios para o cadastro.',
                  confirmButtonColor: '#0039A8',
                  animate: true
                });
                return;
            }
            
            

            adopterService.update(this.adopter)
            .then(() =>{
                this.$swal({
                  icon: 'success',
                  title: 'Adotante atualizado com sucesso!',
                  confirmButtonColor: '#0039A8',
                  animate: true
                });
                this.$router.push({name: "AdopterController"});
            })
            .catch(error => {
                console.log(error);
                this.$swal({
                  icon: 'error',
                  title: 'Não foi possível atualizar o adotante',
                  confirmButtonColor: '#0039A8',
                  animate: true
                });
            });
        },

        save(){
            (this.registrationMode) ? this.registerAdopter() : this.updateAdopter();
        },
    }    
};
</script>

